<script>
import uninames from '@/assets/unicode_names.js'
import titles from '@/assets/titles.js'
import ipaChart from '@/assets/ipaChart.js'
import impossible from '@/assets/impossible.js'
import gfr from '@/assets/graphies/fr.js'
import gen from '@/assets/graphies/en.js'
import gzh from '@/assets/graphies/zh.js'
import sounds from '@/assets/sounds.js'
// import description from '@/assets/description.js'
import placesOfArticulation from './PlacesOfArticulation.vue'
export default {
  name: 'Chart',
  components: {
    placesOfArticulation
  },
  data: function () {
    return {
      gridCols: 34,
      gridRows: 22,
      langs: { en: 'English', fr: 'Français', zh: '中文' },
      ipaList: { en: 'English IPA (Received Pronunciation)', fr: 'API français', zh: '中文国际音标' },
      ipaLang: 'en',
      // External variables
      titles: titles,
      graphies: { en: gen, fr: gfr, zh: gzh },
      // Internal variables
      uniname: '',
      graphie: '',
      coord: [],
      poa: [],
      symbol: '',
      selectedCellOptions: ['link', 'audio', 'none'],
      selectedCell: 'none'
    }
  },
  mounted () {
    // Fill grid
    for (let k in ipaChart) {
      let cell = this.gridCols * (ipaChart[k][0] - 1) + ipaChart[k][1] - 1
      // set content
      this.$refs.grid[cell].innerText = k
      // set class
      let symClass = ['api-symbol']
      if (this.graphies[this.ipaLang][k]) symClass.push('selected-lang')
      if (sounds[k]) symClass.push('sound')
      this.$refs.grid[cell].classList.add(...symClass)
      // set click
      this.$refs.grid[cell].addEventListener('click', this.apiCell.bind(event, k), false);
      // set  places of articulation
      this.$refs.grid[cell].addEventListener('mouseover', this.ppoa.bind(event, k), false);
      this.$refs.grid[cell].addEventListener('mouseleave', this.leave.bind(event, k), false);
    }
    // Fill impossible
    for (let i in impossible) {
      let cell = this.gridCols * (impossible[i][0] - 1) + impossible[i][1] - 1
      this.$refs.grid[cell].classList.add('imp')

    }

  },
  methods: {
    setSelectedLang: function () {
      for (let k in ipaChart) {
        let cell = this.gridCols * (ipaChart[k][0] - 1) + ipaChart[k][1] - 1
        this.graphies[this.ipaLang][k] ?
          this.$refs.grid[cell].classList.add('selected-lang') :
          this.$refs.grid[cell].classList.remove('selected-lang')
      }
    },
    leave: function () {
      this.symbol = ''
      this.poa = []
    },
    ppoa: function (k) {
      this.symbol = k
      this.poa = titles.places2[Math.floor((ipaChart[k][1] - 1) / 2)][1]
    },
    setPoa: function (p) {
      let poa = []
      for (let i = 0; i < p.length; i++) {
        poa.push(p[i])
      }
      this.poa = poa
    },
    openLink: function (article) {
      window.open("https://" + this.ipaLang + ".wikipedia.org/wiki/" + article, "_blank");
    },
    apiCell: function (symbol) {
      // Open link
      if (this.selectedCell == 'link') this.openLink(symbol)
      // play sound
      if (this.selectedCell == 'audio' && sounds[symbol]) {
        var audio = new Audio('http://' + sounds[symbol]);
        audio.play();
      }
      // Show Unicode name (for single character)
      if (uninames[symbol]) {
        this.uniname = uninames[symbol][1]
      } else {
        this.uniname = '';
      }
      // Display writings for a symbol
      this.graphie = ''
      if (!this.graphies[this.ipaLang][symbol]) return
      let keys = Object.keys(this.graphies[this.ipaLang][symbol])
      let nameList = '';
      for (let i = 0; i < keys.length; i++) {
        nameList += "<li><span>" + keys[i] + "</span> : " + this.graphies[this.ipaLang][symbol][keys[i]].join(', ') + "</li>";
      }
      //       this.graphie = "<div><a href='" + description[symbol][1] + "'><b>/" + symbol + "/</b></a><br><i>" + description[symbol][0] + "</i></div><ol>" + nameList + "</ol>"
      this.graphie = "<div><b>/" + symbol + "/</b></div><ol>" + nameList + "</ol>"
    },
  }
}
</script>

